@font-face {
  font-family: "Gilroy-Regular";
  src: url("assets/fonts/Gilroy-Regular.woff2") format("woff2"), url("assets/fonts/Gilroy-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "Gilroy-Medium";
  src: url("assets/fonts/Gilroy-Medium.woff2") format("woff2"), url("assets/fonts/Gilroy-Medium.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "Gilroy-Bold";
  src: url("assets/fonts/Gilroy-Bold.woff2") format("woff2"), url("assets/fonts/Gilroy-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
}
@font-face {
  font-family: "Gilroy-Black";
  src: url("assets/fonts/Gilroy-Black.woff2") format("woff2"), url("assets/fonts/Gilroy-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-stretch: normal;
}

body {
  margin:0;
  padding:0;
  font-family: -apple-system, 'Gilroy-Regular', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ffffff;
}
 
.pageloader {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 8;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background: #294660;
  text-align: center;
}
.pageloader div {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.pageloader img 
{
  width: 72px; margin: 0 auto; display: inline-block; border: 0;
}


.App {
  display: block;
  float: left;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #294660;
}

.wrapper {
  display: flow-root;
  width: 96%;
  margin: 0 auto;
  float: none;
}


@media only screen and (min-width: 1601px) {
  .App {
    display: flow-root;
    max-width: 1600px;
    margin: 0 auto;
    float: none;
  }
}


