.header {
    position: relative;
    background:#284A6D url("../images/bg-top.png") no-repeat center center;
    background-size: cover;
    width: 100%;
    float: left;
    padding: 1rem 0 150px 0;
  }
  .header .wrapper {
    max-width: 1040px;
  }
  .header:after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 150px;
    background: linear-gradient(180deg, rgba(35, 71, 101, 0) 0%, #294660 90%);
  }


